/* Empty front matter tags above are required to signal to Jekyll that this file should be interpolated for Liquid Syntax */

@charset "utf-8";

/* Define defaults for each variable you need to reference in static output scss */

$lm-data-length: 414;
$lm-marq-mxht: $lm-data-length * 1%;

.header {
  position: fixed;
  left: 0;
  top: 0;
  width: 100%;
  background-color: #000080;
  color: red;
  text-align: center;
}
.backdiv {
  /* background-image:url("/assets/_grungy-american-flag-background.jpg"); */
  background-image: url("/assets/flag_table_background.jpg");
  /* opacity:0.7; */
  position: fixed;
  top: 0;
  bottom: 0;
  right: 0;
  left: 0;
  height: 100%;
  width: 100%;
  background-repeat: no-repeat;
  background-size: cover;
  z-index: -1;
}
.footer {
  position: fixed;
  left: 0;
  bottom: 0;
  width: 100%;
  background-color: #000080;
  color: red;
  text-align: center;
}
